<template>
	<view class="order-create-container">
		<view class="room-preview-card">
			<image :src="getImageUrl(roomType.imageUrl)" mode="aspectFill" class="room-img"></image>
			<view class="room-detail">
				<text class="hotel-name">{{ hotelInfo.nameCn || 'Âä†ËΩΩ‰∏≠...' }}</text>
				<text class="room-type-name">{{ roomType.typeName || 'ÊàøÂûãÂä†ËΩΩ‰∏≠...' }}</text>
				<view class="date-info">
					<text>{{ inDateText }} ÂÖ•‰Ωè - {{ outDateText }} Á¶ªÂ∫ó</text>
					<text class="d-count">ÂÖ±{{ orderForm.totalDays }}Êôö</text>
				</view>
			</view>
		</view>

		<view class="order-form">
			<view class="form-row">
				<text class="label">ÊàøÈó¥Êï∞Èáè</text>
				<view class="stepper">
					<view class="step-btn" :class="{disabled: orderForm.roomCount <= 1}" @click="changeCount(-1)">-
					</view>
					<text class="count-num">{{ orderForm.roomCount }}</text>
					<view class="step-btn" @click="changeCount(1)">+</view>
				</view>
			</view>

			<view class="form-row">
				<text class="label">ÂÖ•‰Ωè‰∫∫ÂßìÂêç</text>
				<input class="input-field" v-model="orderForm.guestName" placeholder="ÂßìÂêçÔºåÊØèÈó¥ÊàøÈúÄÂ°´ÂÜô1‰∫∫"
					placeholder-style="color:#ccc;" />
			</view>

			<view class="form-row">
				<text class="label">ËÅîÁ≥ªÊâãÊú∫</text>
				<input class="input-field" type="number" v-model="orderForm.guestPhone" maxlength="11"
					placeholder="Áî®‰∫éÊé•Êî∂Á°ÆËÆ§Áü≠‰ø°" placeholder-style="color:#ccc;" />
			</view>
		</view>

		<view class="pay-method-section">
			<view class="section-title">ÊîØ‰ªòÊñπÂºè</view>
			<radio-group @change="onPayChange">
				<label class="pay-option">
					<view class="pay-left"><text class="icon">üü¢</text>ÂæÆ‰ø°ÊîØ‰ªò</view>
					<radio value="wechat" :checked="payWay === 'wechat'" color="#0086F6" />
				</label>
				<label class="pay-option">
					<view class="pay-left"><text class="icon">üîµ</text>ÊîØ‰ªòÂÆù</view>
					<radio value="alipay" :checked="payWay === 'alipay'" color="#0086F6" />
				</label>
			</radio-group>
		</view>

		<view class="submit-bar">
			<view class="price-summary">
				<text class="total-label">ÊÄªËÆ°</text>
				<text class="currency">¬•</text>
				<text class="total-price">{{ referenceTotal }}</text>
			</view>
			<button class="submit-btn" @click="submitOrder">Êèê‰∫§ËÆ¢Âçï</button>
		</view>
	</view>
</template>

<script>
	import {
		getHotel
	} from '@/api/info.js'
	import {
		getType
	} from '@/api/type.js'
	import {
		addOrder
	} from '@/api/order.js'

	export default {
		data() {
			return {
				hotelId: null,
				roomTypeId: null,
				hotelInfo: {},
				roomType: {},
				payWay: 'wechat',
				orderForm: {
					userId: null, // ÂÆûÈôÖÂºÄÂèëÂ∫î‰ªéÁ≥ªÁªüËé∑ÂèñÂΩìÂâçÁôªÂΩïÁî®Êà∑ID
					hotelId: null,
					roomTypeId: null,
					roomCount: 1,
					checkInDate: '', // Â≠òÂÇ®Ê†ºÂºè YYYY-MM-DD
					checkOutDate: '', // Â≠òÂÇ®Ê†ºÂºè YYYY-MM-DD
					guestName: '',
					guestPhone: '',
					totalDays: 1
				}
			};
		},
		computed: {
			// ÂâçÁ´ØÈ¢ÑËßàÊÄª‰ª∑
			referenceTotal() {
				const price = parseFloat(this.roomType.price || 0);
				return (price * this.orderForm.roomCount * this.orderForm.totalDays).toFixed(2);
			},
			// ‚ú® Êñ∞Â¢ûÔºöÁïåÈù¢ÊòæÁ§∫ÁöÑÂÖ•‰ΩèÊó•Êúü (MM-DD)
			inDateText() {
				return this.orderForm.checkInDate ? this.orderForm.checkInDate.substring(5) : '';
			},
			// ‚ú® Êñ∞Â¢ûÔºöÁïåÈù¢ÊòæÁ§∫ÁöÑÁ¶ªÂ∫óÊó•Êúü (MM-DD)
			outDateText() {
				return this.orderForm.checkOutDate ? this.orderForm.checkOutDate.substring(5) : '';
			}
		},
		onLoad(options) {
			const realUserId = uni.getStorageSync('userId');

			if (!realUserId) {
				// Â¶ÇÊûúÊ≤°ÁôªÂΩïÔºåÂºïÂØºÂéªÁôªÂΩïÈ°µÊàñÊä•Èîô
				uni.showToast({
					title: 'ËØ∑ÂÖàÁôªÂΩï',
					icon: 'none'
				});
				// setTimeout(() => { uni.navigateTo({ url: '/pages/login/login' }) }, 1500);
			} else {
				this.orderForm.userId = realUserId; // ‚ú® ËµãÂÄºÁúüÂÆû ID
				console.log(this.orderForm.userId)
			}
			// 1. Êé•Êî∂‰ªéËØ¶ÊÉÖÈ°µ (detail.vue) ‰º†Êù•ÁöÑÊ†áÂáÜÂèÇÊï∞
			if (options.hotelId && options.roomTypeId) {
				this.hotelId = options.hotelId;
				this.roomTypeId = options.roomTypeId;

				this.orderForm.hotelId = options.hotelId;
				this.orderForm.roomTypeId = options.roomTypeId;
				this.orderForm.checkInDate = options.checkIn; // '2026-03-01'
				this.orderForm.checkOutDate = options.checkOut; // '2026-03-03'
				this.orderForm.totalDays = parseInt(options.days) || 1;
			}

			// 2. ‚ú® ‰øÆÊ≠£ÔºöË∞ÉÁî®Ê≠£Á°ÆÁöÑÊï∞ÊçÆÂä†ËΩΩÊñπÊ≥ï
			this.fetchStaticData();
		},
		methods: {
			async fetchStaticData() {
				uni.showLoading({
					title: 'Âä†ËΩΩ‰∏≠...'
				});
				try {
					const [hRes, tRes] = await Promise.all([
						getHotel(this.hotelId),
						getType(this.roomTypeId)
					]);
					if (hRes.code === 200) this.hotelInfo = hRes.data;
					if (tRes.code === 200) this.roomType = tRes.data;
				} catch (e) {
					console.error('ÈùôÊÄÅÊï∞ÊçÆÂä†ËΩΩÂ§±Ë¥•', e);
				} finally {
					uni.hideLoading();
				}
			},

			changeCount(delta) {
				const res = this.orderForm.roomCount + delta;
				if (res >= 1) this.orderForm.roomCount = res;
				// Ê≥®ÊÑèÔºöÊ≠§Â§Ñ‰∏çËøõË°å‰∏äÈôêÊã¶Êà™ÔºåÁî± submitOrder ÊçïËé∑ÂêéÁ´ØÂ∫ìÂ≠òÊä•Èîô
			},

			async submitOrder() {
				// 1. ÂøÖÂ°´Ê†°È™å
				if (!this.orderForm.guestName || !this.orderForm.guestPhone) {
					return uni.showToast({
						title: 'ËØ∑Â°´ÂÜôÂÆåÊï¥‰ø°ÊÅØ',
						icon: 'none'
					});
				}

				uni.showLoading({
					title: 'Ê≠£Âú®Êèê‰∫§...'
				});

				try {
					// 2. Êèê‰∫§Êï∞ÊçÆÂà∞ÂêéÁ´Ø
					// ÂêéÁ´Ø‰ºöËá™Âä®Ê†πÊçÆÊó•ÊúüÊ†°È™åÂ∫ìÂ≠òÂπ∂ËÆ°ÁÆóÈáëÈ¢ù
					const res = await addOrder({
						userId: this.orderForm.userId,
						hotelId: this.orderForm.hotelId,
						roomTypeId: this.orderForm.roomTypeId,
						roomCount: this.orderForm.roomCount,
						checkInDate: this.orderForm.checkInDate,
						checkOutDate: this.orderForm.checkOutDate,
						guestName: this.orderForm.guestName,
						guestPhone: this.orderForm.guestPhone
					});

					// 3. Â§ÑÁêÜÁªìÊûú
					if (res.code === 200) {
						uni.showToast({
							title: '‰∏ãÂçïÊàêÂäü',
							icon: 'success'
						});
						// Âª∂ËøüË∑≥ËΩ¨ÔºåËÆ©Áî®Êà∑ÁúãÊ∏ÖÊèêÁ§∫
						setTimeout(() => {
							uni.navigateTo({
								url: '/pages/order/order'
							}); // Ë∑≥ËΩ¨Ëá≥‰Ω†ÁºñÂÜôÁöÑËÆ¢ÂçïÂàóË°®È°µ
						}, 1500);
					} else {
						// ‚ú® Ê†∏ÂøÉÈÄªËæëÔºöÁõ¥Êé•Â±ïÁ§∫ÂêéÁ´ØÊäõÂá∫ÁöÑÂ∫ìÂ≠ò‰∏çË∂≥‰ø°ÊÅØ
						// ‰æãÂ¶ÇÔºö"Êä±Ê≠âÔºåËØ•Êó∂ÊÆµÊàøÊ∫êÁÅ´ÁàÜÔºå‰ªÖÂâ© X Èó¥"
						uni.showModal({
							title: 'È¢ÑËÆ¢Â§±Ë¥•',
							content: res.msg || '‰∏ãÂçïÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
							showCancel: false
						});
					}
				} catch (e) {
					uni.showToast({
						title: 'ÁΩëÁªúÁπÅÂøôÔºåËØ∑Á®çÂêéÂÜçËØï',
						icon: 'none'
					});
				} finally {
					uni.hideLoading();
				}
			},

			getImageUrl(url) {
				if (!url) return '/static/default-hotel.png';
				const firstUrl = url.split(',')[0];
				return firstUrl.startsWith('http') ? firstUrl : 'http://localhost:8080' + firstUrl;
			},
			onPayChange(e) {
				this.payWay = e.detail.value;
			}
		}
	};
</script>

<style scoped>
	.order-create-container {
		background: #f8f9fb;
		min-height: 100vh;
		padding: 24rpx;
	}

	.room-preview-card {
		background: #fff;
		border-radius: 20rpx;
		padding: 24rpx;
		display: flex;
		align-items: center;
		margin-bottom: 24rpx;
	}

	.room-img {
		width: 150rpx;
		height: 150rpx;
		border-radius: 12rpx;
		flex-shrink: 0;
		background: #f0f0f0;
	}

	.room-detail {
		margin-left: 24rpx;
		flex: 1;
	}

	.hotel-name {
		font-size: 24rpx;
		color: #999;
		margin-bottom: 8rpx;
		display: block;
	}

	.room-type-name {
		font-size: 32rpx;
		font-weight: bold;
		color: #333;
		margin-bottom: 12rpx;
		display: block;
	}

	.date-info {
		font-size: 24rpx;
		color: #0086F6;
		background: #f0f8ff;
		padding: 8rpx 16rpx;
		border-radius: 8rpx;
		display: inline-block;
	}

	.order-form,
	.pay-method-section {
		background: #fff;
		border-radius: 20rpx;
		padding: 0 30rpx;
		margin-bottom: 24rpx;
	}

	.form-row {
		display: flex;
		align-items: center;
		justify-content: space-between;
		height: 110rpx;
		border-bottom: 1rpx solid #f8f8f8;
	}

	.form-row:last-child {
		border-bottom: none;
	}

	.label {
		font-size: 28rpx;
		color: #333;
	}

	.input-field {
		flex: 1;
		text-align: right;
		font-size: 28rpx;
		color: #333;
	}

	.stepper {
		display: flex;
		align-items: center;
		border: 1rpx solid #eee;
		border-radius: 8rpx;
	}

	.step-btn {
		width: 60rpx;
		height: 60rpx;
		text-align: center;
		line-height: 56rpx;
		font-size: 36rpx;
		background: #f9f9f9;
		color: #666;
	}

	.step-btn.disabled {
		color: #ccc;
	}

	.count-num {
		width: 80rpx;
		text-align: center;
		font-size: 30rpx;
		font-weight: bold;
	}

	.section-title {
		font-size: 28rpx;
		font-weight: bold;
		padding: 30rpx 0 20rpx;
		border-bottom: 1rpx solid #f8f8f8;
	}

	.pay-option {
		display: flex;
		align-items: center;
		justify-content: space-between;
		height: 100rpx;
	}

	.pay-left {
		display: flex;
		align-items: center;
		font-size: 28rpx;
	}

	.icon {
		margin-right: 16rpx;
		font-size: 32rpx;
	}

	.submit-bar {
		position: fixed;
		left: 0;
		bottom: 0;
		width: 100%;
		height: 120rpx;
		background: #fff;
		border-top: 1rpx solid #eee;
		display: flex;
		align-items: center;
		justify-content: space-between;
		padding: 0 40rpx;
		box-sizing: border-box;
		z-index: 99;
	}

	.total-label {
		font-size: 24rpx;
		color: #666;
		margin-right: 10rpx;
	}

	.currency {
		color: #ff4d4f;
		font-size: 28rpx;
		font-weight: bold;
	}

	.total-price {
		color: #ff4d4f;
		font-size: 44rpx;
		font-weight: bold;
	}

	.submit-btn {
		background: linear-gradient(90deg, #00B4F6, #0086F6);
		color: #fff;
		border-radius: 44rpx;
		height: 88rpx;
		line-height: 88rpx;
		padding: 0 60rpx;
		margin: 0;
		font-weight: bold;
		font-size: 30rpx;
	}
</style>